<template>
  <div>
    <a-layout id="components-layout-demo-fixed">
      <!-- <a-layout-header :style="{ position: 'fixed',  zIndex: 1, width: '100%'}">
        <div class="title">电影名称</div>
      </a-layout-header>-->
      <a-layout-content :style="{  marginTop: '0' }">
        <div>
          <div class="custom-video-outer-box">
            <videoPlayer
              class="video-player-box"
              ref="videoPlayer"
              :options="playerOptions"
              :playsinline="true"
            ></videoPlayer>
          </div>
           <div style="text-align:left;padding:8px;font-weight:bold;font-size:20px; color:black;">{{title}}</div>

          <!-- <a-row  type="flex" justify="space-between">
                <a-col :span="16">
                    <div   style="item-alignleft;width:100%; color:#333;font-size:16px; font-weight:bold;padding:8px 0 0 4px; white-space: nowrap;overflow: hidden;text-overflow: ellipsis;">{{title}}</div>
                </a-col>
                <a-col :span="8">

                   <a-select defaultValue="源1" style="margin:4px;item-align:right;" @change="handleChange">
                    <a-select-option v-for="item,index in m3u8list"  :value="item.a2">{{item.a1}}</a-select-option>
                   </a-select>

                </a-col>
          </a-row>-->
          <div style="text-align:left;padding:8px;font-weight:bold;font-size:18px; color:#444;">相关推荐</div>
          <div v-for="item in sublist" @click="onclick2(item.movieid)" :key="item">
            <div style="text-align:left;color:#444;background: white;margin:4px 8px">
              <a-row>
                <a-col :span="6">
                  <img
                    style="width:100%"
                    :src="'http://super-search.u.qiniudn.com/king'+item.movieid + '_DetailCoverimg.jpg'"
                    onError="this.src='https://i.loli.net/2019/08/30/AnZb8YCGdvsNO3P.jpg'"
                    alt
                  />
                </a-col>
                <a-col :span="18">
                  <div style="padding-left:8px">
                    <div
                      style="font-size:16px;color:#333;padding-bottom:20px;padding-top:8px;font-weight:bold"
                    >{{item.moviename}}</div>
                    <div class="a2">{{item.movietype}}</div>
                  </div>
                </a-col>
              </a-row>
            </div>
          </div>
        </div>
      </a-layout-content>

      <a-layout-footer style="textAlign: center">电影大全 ©2019 Created by 91uwp.com</a-layout-footer>
    </a-layout>
    <div @click="onclick" class="menu1">
      <div class="sss" style="margin:16px;">
        <a-icon style="font-size:18px;color:white;padding-top:10px" type="home" />
        <div style="font-size:12px">主页</div>
      </div>
    </div>
  </div>
</template>
<script>
import "video.js/dist/video-js.css";
import { videoPlayer } from "vue-video-player";
import "videojs-contrib-hls.js/src/videojs.hlsjs";
export default {
  data() {
    return {
      sublist: [],
      defaultValue: null,
      visible: false,
      title: "",
      m3u8list: [],
      menus: {},
      playerOptions: {
        fluid: false,
        autoplay: false,
        sources: null,
        poster: "",
        notSupportedMessage: "此视频暂无法播放，请稍后再试",
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //全屏按钮
        }
      }
    };
  },
  components: {
    videoPlayer
  },
  created() {
    this.getdata(this.$route.query.id);
  },
  methods: {
    getdata(id) {
      var that = this;
      that.axios.get("https://service-ny42jrk1-1251342364.ap-shanghai.apigateway.myqcloud.com/release/getvideo?movieid=" + id).then(res => {
        var m3u8list = [];
        var lists = [];
        that.title = res.data["title"];

        var items = res.data["m3u8list"];
        for (var i in items) {
          var j = parseInt(i) + 1;
          lists.push({
            a1: "源" + j,
            a2: items[i]
          });
          m3u8list.push({
            type: "",
            src: items[i]
          });
        }
        if (lists != []) {
          that.defaultValue = lists[0].a1;
        }
        that.m3u8list = lists;
        that.getsub(that.title);
        that.playerOptions.sources = m3u8list;
        that.playerOptions.autoplay = true;
        console.log(that.playerOptions.sources);
      });
    },
    getsub(title) {
      var that = this;

      that.axios
        .get(
          "search/release/getsearch?keyword=" + title.substring(0, 1) + "&num=5"
        )
        .then(res => {
          that.sublist = JSON.parse(res.data).results;
        });
    },
    onclick() {
      this.$router.push({ name: "HelloWorld" });
    },
    onclick2(id) {
      
      this.$router.push({ name: "VideoPlayer", query: { id: id } });
      this.getdata(id);
    },
    handleChange(e) {
      var that = this;
      console.log(e);
      that.playerOptions.sources = {
        type: "",
        src: e
      };
    }
  }
};
</script>
 
<style   scoped>
.menu1 {
  position: fixed;

  width: 90px;
  top: 80vh;
  left: 75vw;
  right: 0;
  bottom: 0;
  height: 90px;
  z-index: 2;
}
.sss {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  right: 0;
  top: 90vh;
  vertical-align: middle;
  background: #1890ff;
  box-shadow: 1px 1px 1px #777;
}
.a {
  background: red;
}
.a2 {
  overflow: hidden;

  text-overflow: ellipsis;

  -webkit-line-clamp: 2;
  display: -webkit-box;

  -webkit-box-orient: vertical;
}
.articles-page {
  position: relative;
}
.video-player-box {
  height: 100%;
  width: 100%;
}
.play_box {
  position: relative;
  height: 100%;
  width: 100%;
}

.post-date {
  color: #989898;
  font-size: 12px;
}

.tag-success {
  color: #0e4933;
}
.tag-draft {
  color: #4c4c4c;
}
</style>

<style scoped>
.aaa {
  z-index: 10;
}
.back {
  align-items: left;
}
.header {
  font-size: 16px;
  margin-top: 18px;
}
</style>

<style scoped>
.title {
  text-align: left;
  padding-left: 30px;
  font-weight: bold;
  font-size: 16px;
  width: 100%;
  overflow: hidden; /*超出部分隐藏*/
  white-space: nowrap; /*不换行*/
  text-overflow: ellipsis; /*超出部分文字以...显示*/
}
</style>
<style  >
.video-player-box {
  height: 100%;
  width: 100%;
}
.card-container {
  background: #f5f5f5;
  overflow: hidden;
  padding: 24px;
  text-align: left;
}
.custom-video-outer-box {
  width: 100%;
  height: 56vw;
}
.progress-slider {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 18px;
  line-height: 18px;
}
.time {
  margin-left: 25px;
}
.full-screen {
  margin-left: 25px;
  line-height: 60px;
}

.ivu-progress-outer {
  padding: 0 10px !important;
}

.vjs-big-play-button {
  height: 80px !important;
  width: 80px !important;
  line-height: 80px !important;
  text-align: center;
  background: rgba(0, 0, 0, 0.8) !important;
  border-radius: 50% !important;
  top: 50% !important;
  left: 50% !important;
  margin-left: -40px !important;
  margin-top: -40px !important;
}
#vjs_video_3 {
  max-height: 100% !important;
  width: 100% !important;
  height: 100% !important;
}
.video-player-box > div {
  height: 100% !important;
  width: 100% !important;
}
.video-js .vjs-big-play-button {
  font-size: 5em !important;
}
video {
  max-height: 100% !important;
}
</style> 


