<template>
  <div>
    <a-layout id="target">
      <a-layout>
        <a-layout-header
          :style="{ background: '#001529', padding: 0 ,position: 'fixed', zIndex: 1, width: '100%'}"
        >
          <div style="position: relative;">
            <div class="title">电影大全UWP</div>
            <a-icon
              style="position:absolute; right:15px; top:15px ; font-size: 22px;"
              type="search"
              @click="showDrawer"
            />
          </div>
        </a-layout-header>
        <a-layout-content :style="{ margin: '80px 4vw  0 4vw' }">
          <div :style="{ padding: '12px', background: '#fff'  }">
            <a-list
              style="min-height: 100vh;"
              :loading="isloading"
              :grid="{ gutter: 4, xs: 2, sm: 3, md: 4, lg: 6, xl: 6, xxl: 6 }"
              :dataSource="data"
            >
              <a-list-item slot="renderItem" @ slot-scope="item">
                <a-card @click="onclick(item.movieid)" hoverable style="text-align:left">
                  <img
                    :src="'http://super-search.u.qiniudn.com/king'+item.movieid + '_DetailCoverimg.jpg'"
                    slot="cover"
                    style="  "
                    onError="this.src='https://i.loli.net/2019/08/30/AnZb8YCGdvsNO3P.jpg'"
                  />
                  <a-card-meta style="white-space:nowrap;height:50px" :title="item.moviename">
                    <template slot="description">{{item.movietype }}</template>
                  </a-card-meta>
                </a-card>
              </a-list-item>
            </a-list>
            <a-pagination @change='onchange' v-model="current" :defaultPageSize='24' :total="allcount" />
          </div>
        </a-layout-content>
        <a-layout-footer style="textAlign: center">电影大全 ©2019 Created by 91uwp.com</a-layout-footer>
      </a-layout>
    </a-layout>
    <div class="menu1">
      <a-button type="primary" @click="toggleCollapsed" style="margin:16px;">
        <a-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" />
      </a-button>
      <a-menu
        @select="onselect"
        style="height:100%; "
        theme="dark"
        mode="inline"
        :inlineCollapsed="collapsed"
        :defaultSelectedKeys="['剧情']"
      >
        <a-menu-item key="剧情">
          <!-- <a-icon type="user" /> -->
          <span class="nav-text">剧情</span>
        </a-menu-item>
        <a-menu-item key="喜剧">
          <!-- <a-icon type="video-camera" /> -->
          <span class="nav-text">喜剧</span>
        </a-menu-item>
        <a-menu-item key="动作">
          <!-- <a-icon type="upload" /> -->
          <span class="nav-text">动作</span>
        </a-menu-item>
        <a-menu-item key="爱情">
          <!-- <a-icon type="user" /> -->
          <span class="nav-text">爱情</span>
        </a-menu-item>
        <a-menu-item key="犯罪">
          <!-- <a-icon type="user" /> -->
          <span class="nav-text">犯罪</span>
        </a-menu-item>
        <a-menu-item key="冒险">
          <!-- <a-icon type="user" /> -->
          <span class="nav-text">冒险</span>
        </a-menu-item>
      </a-menu>
    </div>

    <a-drawer
      title="电影搜索"
      placement="top"
      height="100%"
      :closable="false"
      @close="onClose"
      :visible="visible"
    >
      <div>
        <a-input-search placeholder="Search..." style="width: 100%;" @search="onsearch" />
        <div
          style="overflow-y: auto;overflow-x:hidden;height:80vh;position: relative;padding-top:8px;"
        >
          <a-list
            :loading="isloading1"
            :grid="{ gutter: 4, xs: 2, sm: 3, md: 4, lg: 6, xl: 6, xxl: 6 }"
            :dataSource="data1"
          >
            <a-list-item slot="renderItem" @ slot-scope="item">
              <a-card @click="onclick(item.movieid)" hoverable style="text-align:left">
                <img
                  :src="'http://super-search.u.qiniudn.com/king'+item.movieid + '_DetailCoverimg.jpg'"
                  slot="cover"
                  style="  "
                  onError="this.src='https://i.loli.net/2019/08/30/AnZb8YCGdvsNO3P.jpg'"
                />
                <a-card-meta style="white-space:nowrap;height:50px" :title="item.moviename">
                  <template slot="description">{{item.movietype+' '}}</template>
                </a-card-meta>
              </a-card>
            </a-list-item>
          </a-list>
          <!-- <div v-for="item in 100">
                 <div style="display: inline-block;">555</div>
          </div>-->
        </div>
        <div
          style="position: absolute;bottom:30px;text-align: center;margin-left:auto; margin-right:auto;width:100%;left:0;right:0"
        >
          <a-button @click="visible=false" shape="circle" icon="close"></a-button>
        </div>
      </div>
    </a-drawer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      current: 1,
      allcount:0,
      isloading: true,
      isloading1: false,
      visible: false,
      collapsed: true,
      data1: [],
      data: []
    };
  },
  mounted() {
    var that = this;
    that.axios.get("https://service-ny42jrk1-1251342364.ap-shanghai.apigateway.myqcloud.com/release/getlist?type=&key=&page=0").then(res => {
      console.log("ssss");
      that.data = JSON.parse(res.data).results;
      that.allcount = JSON.parse(res.data).count;
      that.isloading = false;
    });
  },
  methods: {
    onsearch(e) {
      var that = this;
       that.isloading1 =true
      that.axios.get("https://service-ny42jrk1-1251342364.ap-shanghai.apigateway.myqcloud.com/release/getsearch?keyword="+e).then(res => {
        that.data1 = JSON.parse(res.data).results;
        that.isloading1 = false;
      });
       
    },
    toggleCollapsed() {
      this.collapsed = !this.collapsed;
    },
    onCollapse(collapsed, type) {
      console.log(collapsed, type);
    },
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    onclick(id) {
      this.$router.push({ name: "VideoPlayer", query: { id: id } });
    },
    onchange(e)
    {
      var that = this
       var key = that.key==null?'':that.key
     
       that.getdata("movietype", e, key);
    //  that.axios.get("https://service-ny42jrk1-1251342364.ap-shanghai.apigateway.myqcloud.com/release/getlist?type=movietype&key="+key+"&page="+e).then(res => {
    //   console.log("ssss");
    //   that.data = JSON.parse(res.data).results;
    //   that.allcount = JSON.parse(res.data).count;
    //   that.isloading = false;
    // });

    },
    onselect(e) {
      var that = this;
      that.current=1;
      var key = e.key;
      that.key = key
      that.getdata("movietype", "0", key);
      console.log(e);
    },
    getdata(type, page, key) {
      var that = this;
      target.scrollIntoView();
      that.data = [];
       
      that.isloading = true;
      that.axios
        .get(
          "https://service-ny42jrk1-1251342364.ap-shanghai.apigateway.myqcloud.com/release/getlist?type=" + type + "&key=" + key + "&page=" + page
        )
        .then(res => {
          that.isloading = false;
           that.allcount = JSON.parse(res.data).count;
          that.data = JSON.parse(res.data).results;
          if (that.collapsed == false) that.collapsed = true;
          console.log(JSON.parse(res.data).results);
        });
    }
  }
};
</script>

<style>
.menu1 {
  position: fixed;
  left: 0;

  bottom: 0;
  top: 0;
  /* background: #001529; */
  z-index: 2;
}
.title {
  font-size: 15px;
  font-weight: bold;
  /* padding-right: 18px; */
  /* text-align: right; */
}
.nav-text {
  padding: 0 32px 0 10px;
}
</style>



